.button_list {
    padding: 8px 16px;
    margin: 0px 5px 10px 10px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s, color 0.3s;
  }
  
  .button_list:hover {
    background-color: #0056b3;
  }